import confExplorer.ConfExplorerApp
import kotlinx.css.*
import kotlinx.css.properties.border
import kotlinx.html.js.onClickFunction
import org.w3c.dom.MediaQueryList
import react.*
import react.dom.div
import react.dom.header
import styled.*
import kotlin.browser.window

data class View(val name: String, val href: String?)

external interface RootState : RState {
    var viewList: List<View>
    var currentView: String?
    var columns: Int
}

class Root: RComponent<RProps, RootState>() {
    override fun RBuilder.render() {
        header {
            styledH1 {
                css {
                    textAlign = TextAlign.center
                    cursor = Cursor.pointer
                    padding(20.px)
                    border(1.px, BorderStyle.dotted, Color.indianRed)
                }
                attrs {
                    onClickFunction = {
                        setState { currentView = null }
                    }
                }
                +"Movie Jo Web Study" }
        }
        div {
            state.viewList.withIndex().chunked(state.columns).map { chunk ->
                div {
                    chunk.map { (idx, view) ->
                        styledDiv {
                            css {
                                boxSizing = BoxSizing.borderBox
                                display = Display.inlineFlex
                                width = (100/state.columns).pct
                            }
                            view.href?.let { href ->
                                styledA {
                                    css {
                                        cursor = Cursor.pointer
                                        border(1.px, BorderStyle.dotted, Color("#bbbbbb"))
                                        width = 100.pct
                                        padding(5.px)
                                        margin(5.px)
                                    }
                                    attrs.href = href
                                    +"${idx + 1}. ${view.name}"
                                }
                            } ?: run {
                                styledDiv {
                                    css {
                                        cursor = Cursor.pointer
                                        border(1.px, BorderStyle.dotted, Color("#bbbbbb"))
                                        width = 100.pct
                                        padding(5.px)
                                        margin(5.px)
                                    }
                                    attrs {

                                        onClickFunction = {
                                            setState {
                                                currentView = view.name
                                            }
                                        }
                                    }
                                    +"${idx + 1}. ${view.name}"
                                }
                            }
                        }
                    }
                }
            }
        }
        child(
            when(state.currentView) {
                "confExplorer" -> ConfExplorerApp::class
                else -> DefaultApp::class
            }
        ) {}
    }

    override fun RootState.init() {
        viewList = listOf(
            View("confExplorer", null),
            View("material", "https://moviejo.github.io/MaterialUI/")
        )

        fun mediaCheck(widthQuery: String, onTrue: Int, onFalse: Int): MediaQueryList {
            val c = window.matchMedia("only screen and ($widthQuery)")
            c.addListener {
                println("media Check ($widthQuery, ${if(c.matches) onTrue else onFalse})")
                setState {
                    columns = if(c.matches) onTrue else onFalse
                } }
            return c
        }
        if (mediaCheck("max-width: 600px", 1, 2).matches) columns = 1
        if (mediaCheck("min-width: 600px", 2, 1).matches) columns = 2
        if (mediaCheck("min-width: 768px", 3, 2).matches) columns = 3
        if (mediaCheck("min-width: 992px", 4, 3).matches) columns = 4
        if (mediaCheck("min-width: 1200px", 6, 4).matches) columns = 6
    }
}

class DefaultApp : RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        div {
            styledH2 {
                css {
                    textAlign = TextAlign.center
                }
                +"Select one of them"
            }
        }
    }
}
